* {
  font-family: Arial, Helvetica, sans-serif;
}


body{
  padding: 60px;
}


input{
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}


/*  clase card */

.card{

  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;


}


.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170 px;
}

/*  clase card-grid */


.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}


